import React, { Component } from "react";
import axios from "axios";
import Loader from "./Loader";
import "./styles.css";
import Searchbar from "./Searchbar/Searchbar";
import ImageGallery from "./ImageGallery/ImageGallery";

export default class App extends Component {
  state = {
    photos: [],
    loading: false,
    query: "",
  };

  componentDidMount() {
    this.setState({ loading: true });

    const apiKey = "18862670-b1698d422e60fbfd1dac31137";
    const query = "";
    const url = `https://pixabay.com/api/?q=${query}&page=1&key=${apiKey}&image_type=photo&orientation=horizontal&per_page=12`;

    axios
      .get(url)
      .then((res) => {
        this.setState({ photos: res.data.hits });
        console.log(res.data.hits);
      })
      .finally(() => this.setState({ loading: false }));
  }

  searchPhotos = (search) => {
    this.setState({ loading: true });
    const { query } = search;

    const apiKey = "18862670-b1698d422e60fbfd1dac31137";
    const url = `https://pixabay.com/api/?q=${query}&page=1&key=${apiKey}&image_type=photo&orientation=horizontal&per_page=12`;

    axios
      .get(url)
      .then((res) => {
        this.setState({ photos: res.data.hits });
        console.log(res.data.hits);
      })
      .finally(() => this.setState({ loading: false }));
  };

  render() {
    const { photos, loading } = this.state;
    return (
      <>
        <Searchbar onSubmit={this.searchPhotos} />
        {loading ? <Loader /> : <ImageGallery photos={photos} />}
      </>
    );
  }
}
